<template>
  <q-select
    :autofocus="autofocus"
    :dense="dense"
    :emit-value="emitValue"
    outlined
    hide-bottom-space
    :multiple="multiple"
    :use-chips="multiple"
    option-value="value"
    option-label="label"
    class="rounded text-body2 input-white text-primary"
    popup-content-class="bg-white rounded-bottom"
    input-class="text-body1 letter-space-normal text-primary"
    :options="selection"
    v-model="modelValue"
    :rules="[(val) => (required ? !!val || '' : true)]"
  >
    <template v-slot:option="scope">
      <q-item
        clickable
        v-bind="scope.itemProps"
        :class="scope.selected ? 'bg-accent text-secondary' : 'text-primary'"
        class="hover-text-secondary hover-bg-accent"
        @click="scope.opt?.action"
      >
        <q-item-section avatar v-if="scope.opt?.icon" style="min-width: unset">
          <q-icon size="xs" :name="scope.opt?.icon" />
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{ scope.opt?.label || scope.opt }}
          </q-item-label>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:selected>
      <span :class="{ ['text-placeholder']: !modelValue?.length }"
        >{{
          multiple && modelValue?.length
            ? `Selected ${modelValue?.length} option${
                modelValue?.length > 1 ? 's' : ''
              }`
            : multiple && !modelValue?.length
            ? placeholder
            : emitValue
            ? selection.filter((e) => e.value === modelValue)?.[0]?.label ||
              placeholder
            : modelValue && typeof modelValue !== 'object'
            ? modelValue
            : modelValue?.label || placeholder
        }}
      </span>
    </template>
  </q-select>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'SelectOption',

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: 'Select option',
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    selection: {
      type: Array,
      default: () => [],
    },
    dense: {
      type: Boolean,
      default: true,
    },
    initModel: {},
    required: {
      type: Boolean,
      default: false,
    },
    emitValue: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      modelValue: null,
    };
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.modelValue = val;
      },
    },
  },

  mounted() {
    this.modelValue = this.initModel;
  },
});
</script>
